export default {
  name: "user_info",
  props: {
    userinfo: undefined,
    visit_self: undefined,
  },
  created() {

  },
  computed: {
    set_user_info: function () {
      try {
        return this.userinfo.userinfo
      } catch (e) {

      }
      return undefined
    },
    is_self_visit: function () {
      try {
        return this.$store.state.user.userinfo.uid == this.userinfo.uid;
      } catch (e) {

      }
      return false;
    }
  }
}
