export default {
  name: "chaobject",
  methods: {
    generate_small_image: function (image_item) {
      if (image_item && image_item['file_hash_id']) {
        var host = this.get_image_host(image_item['nid']);
        return 'https://' + host + '/images/' + image_item['file_hash_id'] + '/5_328_228'
      }
    },
    generate_image_url: function (image_item, size = 'i') {
      if (image_item && image_item['file_hash_id']) {
        var host = this.get_image_host(image_item['nid']);
        return 'https://' + host + '/images/' + image_item['file_hash_id'] + '/' + size;
      }
    },
    generate_tmpimage_url: function (image_item, size = 'i') {
      if (image_item && image_item['file_hash_id']) {
        var host = this.get_image_host(image_item['nid']);
        return 'https://' + host + '/tmp_images/' + image_item['file_hash_id'] + '/' + size;
      }
    },
    get_image_host: function (nid) {
      if (nid in this.image_hosts_conf) {
        host = this.image_hosts_conf[nid]['host'];
        return host;
      }
      if (nid in this.$config.space_confs) {
        var image_conf = this.$config.space_confs[nid]['image'];
        if (image_conf) {
          var host = image_conf['hosts'][0];
          return host;
        }
      }
    },
    get_list: function ({t, p, r, start, limit, cb}) {
      this.$axios.get('/app/cha/list', {
        params: {
          t: t,
          p: p,
          r: r,
          start: start,
          limit: limit
        }
      }).then((response) => {
        if (cb) {
          cb(response)
        }
      }).catch((error) => {
      });
    },
    get_cha: function ({id, cb, from_cache = true, province_latest = false, error_cb = undefined}) {
      var params = {
        id: id,
        type: "xx",
        from_cache: from_cache
      };

      if (province_latest) {
        params['province_latest'] = province_latest;
      }

      this.$axios.get('/app/release/thread', {
        params: params
      }).then((response) => {
        console.log('[get_cha]response:', response);
        if (response.code != 200) {
          if (error_cb) {
            return error_cb(response)
          }
        }
        var thread = response.data.thread;
        var province_latest = response.data.province_latest;
        if (cb) {
          cb(thread, province_latest);
        }
      }).catch(function (error) {
      });
    },
    buy: function ({id, cb, error_cb = undefined}) {
      var post_data = {
        id: id,
        type: "xx",
        item: 'contact'
      };

      this.$axios.post('/app/cha/buy', post_data).then((response) => {
        console.log('[buy]response:', response);
        if (cb) {
          cb(response);
        }
      }).catch(function (error) {
        if (error_cb) {
          return error_cb(error)
        }
      });
    },
    load_view_history: function ({start, limit, uid, cb}) {
      var request_params = {
        start: start,
        limit: limit,
        uid: uid
      }
      this.$axios.get("/app/cha/history", {
        params: request_params
      }).then((response) => {
        if (cb) {
          cb(response);
        }
      }).catch((error) => {

      });
    },
    load_buy: function ({start, limit, uid, cb}) {
      var request_params = {
        start: start,
        limit: limit,
        uid: uid
      }
      this.$axios.get("/app/cha/buy", {
        params: request_params
      }).then((response) => {
        if (cb) {
          cb(response);
        }
      }).catch((error) => {

      });
    }
  }
}
