export default {
  name: "cha_list",
  props: {
    cha_datas: undefined,
    clientWidth: undefined,
    SET_ROW_COUNT: undefined
  },
  data() {
    return {
      class_name: '',
      ROW_COUNT: this.is_mobile() ? 1 : 3,
      show_video_list: undefined
    }
  },
  created() {

    console.log('create cha list:',this.cha_datas);
    this.ROW_COUNT = this.SET_ROW_COUNT;

    setTimeout(() => {
      this.$nextTick(() => {
        this.update_row_count();
        this.update_list();
        this.class_name = this.col_class_name();
      });
    }, 50);
    window.addEventListener('resize', this.on_resize,false);
  },
  destroyed() {
    window.removeEventListener('resize', this.on_resize,false);
  },
  watch: {
    'ROW_COUNT': function () {
      console.log('watch ROW_COUNT change');
      this.update_list();
      this.$forceUpdate();
      this.video_class_name = this.col_class_name();
    },
  },
  methods: {
    on_resize:function (){
      this.update_row_count();
    },
    col_class_name: function () {
      let class_name = 'col-' + parseInt(12 / this.ROW_COUNT);
      console.log('video_col_class_name:', class_name)
      return class_name
    },
    set_default_row_count:function (){
      if(this.is_mobile()){
        this.ROW_COUNT = 1;
      }else {
        this.ROW_COUNT = 4;
      }
    }
    ,
    update_row_count: function () {
      var clientWidth = this.clientWidth;
      console.log("this.$refs['list']:", this.$refs['list']);
      if (!this.$refs['list']) {
        setTimeout(this.update_row_count, 100);
        this.set_default_row_count();
        return this.ROW_COUNT;
      }
      if (this.$refs['list']) {
        clientWidth = this.$refs['list'].clientWidth;
      }
      if (this.SET_ROW_COUNT) {
        this.ROW_COUNT = this.SET_ROW_COUNT;
      } else {
        console.log('clientWidth:', clientWidth);
        if (clientWidth && clientWidth != 0) {

          var ROW_COUNT = parseInt(clientWidth / 288);
          ROW_COUNT = ROW_COUNT < 1 ? 1 : ROW_COUNT;
          ROW_COUNT = ROW_COUNT > 6 ? 6 : ROW_COUNT;
          console.log('ROW_COUNT:', ROW_COUNT);
          this.ROW_COUNT = ROW_COUNT;
          return this.ROW_COUNT;
        }
      }
    },
    update_list: function () {

    }
  }
}
