import chaobject from "@/components/script/chaobject";

export default {
  name: "cha_item",
  mixins: [chaobject],
  props: {
    cha_data: undefined
  },
  methods: function () {

  },
  computed: {
    show_iamge_url: function () {
      if (this.cha_data.images) {
        return this.generate_small_image(this.cha_data.images[0]);
      }
      return undefined
    },
  }
}
